.wrapper {
  overflow: hidden;
}
.h1 {
  font-size: 46px;
  line-height: 110%;
  font-weight: 600;
  letter-spacing: -0.92px;
}
.h2 {
  font-size: 36px;
  line-height: 110%;
  font-weight: 600;
  letter-spacing: -0.72px;
}
.h3 {
  font-size: 26px;
  line-height: 120%;
  font-weight: 500;
  letter-spacing: -0.52px;
}
.btn-brown {
  background: #F2DAAF;
  border-radius: 12px;
  color: #191919;
  &:hover {
    background: #E6B964;
    .btn-brown__arrow {
      background: #F2DAAF;
    }
  }
  span {
    line-height: 100%;
    font-weight: 500;
  }
  .btn-brown__arrow {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #E6B964;
    border-radius: 88px;
    padding: 6px 10px;
    svg {
      width: 9px;
      height: auto;
    }
  }
}
.btn-brown2 {
  background: #E6B964;
  border-radius: 12px;
  &:hover {
    background: #F2DAAF;
  }
  span {
    line-height: 100%;
    font-weight: 500;
  }
}
.btn-gray {
  background: #F1F7F7;
  border-radius: 12px;
  &:hover {
    background: #268B8B;
    color: #fff;
  }
  span {
    line-height: 100%;
    font-weight: 500;
  }
}
.btn-green {
  background: #006B6B;
  border-radius: 12px;
  color: #fff;
  &:hover {
    background: #F1F7F7;
    color: #191919;
  }
  span {
    line-height: 100%;
    font-weight: 500;
  }
}
.btn-white {
  background: #fff;
  border-radius: 12px;
  &:hover {
    background: #F1F7F7;
  }
  span {
    line-height: 100%;
    font-weight: 500;
  }
}
.socials {
  display: flex;
  align-items: center;
  gap: 10px;
}
.social {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F1F7F7;
  border-radius: 10px;
  padding: 5px;
  svg {
    max-width: 22px;
    width: auto;
    height: auto;
    path {
      transition: all 0.3s;
    }
  }
}
.swiper-button-prev,
.swiper-button-next {
  background: #1F8C8C;
  border-radius: 10px;
  opacity: 1 !important;
  &:after {
    display: none;
  }
  &:hover {
    background: #fff;
    svg path {
      stroke: #003737;
    }
  }
  svg {
    width: 9px;
    height: auto;
  }
  svg path {
    transition: all 0.3s;
  }
}
.form {
  &-group {
    input {
      width: 100%;
      height: 58px;
      background: #fff;
      border:  1px solid transparent;
      border-radius: 12px;
      padding: 0 24px;
      font-size: 18px;
      line-height: 188%;
      color: #191919;
      &::placeholder {
        color: #888888;
      }
    }
    textarea {
      width: 100%;
      height: 107px;
      background: #fff;
      border:  1px solid transparent;
      border-radius: 12px;
      padding: 16px 24px;
      font-size: 18px;
      line-height: 188%;
      color: #191919;
      resize: none;
      &::placeholder {
        color: #888888;
      }
    }
  }
  &-btn {
    width: 100%;
    height: 58px;
    padding: 5px 10px;
    &:hover {
      background: #009a9a;
      color: #fff;
    }
    span {
      font-size: 18px;
    }
  }
  &-check {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check-icon.svg");
      }
    }
    label {
      position: relative;
      display: block;
      padding-left: 22px;
      font-size: 10px;
      line-height: 100%;
      color: #A8C3C3;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 10.5px;
        height: 10.5px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        border: 1.4px solid #A8C3C3;
        border-radius: 50%;
      }
      a {
        position: relative;
        z-index: 3;
        &:hover {
          color: #F2DAAF;
        }
      }
    }
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.7);
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: 100%;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      width: calc(100% - 30px) !important;
    }
  }
  &__close {
    position: absolute;
    top: -42px;
    right: 0;
    width: 22px;
    background: transparent;
    padding: 0;
    img {
      width: 100%;
    }
  }
  &__icon {
    width: 200px;
    margin: 0 auto 18px auto;
    img {
      width: 100%;
    }
  }
  &__title {
    font-size: 30px;
    line-height: 110%;
    font-weight: 500;
    letter-spacing: -0.6px;
    color: #006B6B;
  }
  .center-title {
    text-align: center;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 155%;
    margin-top: 8px;
  }
  .form {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 18px;
    &-check {
      margin-top: 8px;
    }
  }
  &__btn {
    width: 100%;
    height: 58px;
    padding: 5px 10px;
    margin-top: 18px;
    &:hover {
      background: #009a9a;
      color: #fff;
    }
    span {
      font-size: 18px;
    }
  }
}
.modal-form {
  .modal__dialog {
    max-width: 480px;
    background: #F1F7F7;
    border-radius: 24px;
    padding: 24px 32px;
  }
}
.modal-price {
  .modal__dialog {
    max-width: 1280px;
    width: calc(100% - 30px);
    background: #fff;
    border-radius: 15px;
    padding: 44px 54px;
    overflow: hidden;
  }
  .modal__close {
    top: 48px;
    right: 54px;
  }
  .modal__title {
    padding-right: 30px;
    color: #191919;
  }
  &__subtitle {
    margin-top: 25px;
  }
  &__overflow {
    margin-top: 25px;
  }
  &__table {
    width: 100%;
    table-layout: fixed;
    border-collapse: collapse;
    thead {
      tr {
        th {
          text-align: start;
          padding-right: 20px;
          font-size: 14px;
          line-height: 140%;
          letter-spacing: -0.28px;
          color: #9C9C9C;
          &:first-child {
            width: 140px;
          }
          &:last-child {
            padding-right: 0;
          }
        }
      }
    }
    tbody {
      tr:first-child {
        td {
          padding-top: 25px;
        }
      }
      tr:last-child {
        td {
          padding-bottom: 0;
        }
      }
      td {
        font-size: 16px;
        line-height: 125%;
        font-weight: 500;
        letter-spacing: -0.32px;
        padding-right: 20px;
        padding-bottom: 20px;
        &:last-child {
          padding-right: 0;
        }
      }
    }
  }
}
.modal-info {
  .modal__dialog {
    max-width: 700px;
    width: calc(100% - 30px);
    background: #fff;
    border-radius: 25px;
    padding: 25px;
  }
  &__img {
    position: relative;
    padding-bottom: 69%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 24px;
    }
  }
  &__desc {
    margin-top: 25px;
  }
  &__title {
    text-align: center;
    font-size: 30px;
    line-height: 110%;
    font-weight: 500;
    letter-spacing: -0.6px;
  }
  &__text {
    font-size: 18px;
    line-height: 155%;
    margin-top: 25px;
  }
}
.breadcrumbs {
  margin-bottom: 24px;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
  }
  &__item {
    font-size: 18px;
    line-height: 100%;
    color: #888;
    &:hover {
      color: #191919;
    }
    &--active {
      &:hover {
        color: #888;
      }
    }
  }
  &__item-arrow {
    font-size: 16px;
    line-height: 100%;
    color: #888;
  }
}
.fancybox__content {
  width: 90% !important;
  height: auto !important;
  max-height: 100% !important;
  img {
    transform: unset !important;
    width: 100% !important;
    height: auto !important;
    max-height: 100% !important;
    object-fit: cover !important;
  }
}
.fancybox__carousel .fancybox__slide.has-video .fancybox__content,
.fancybox__carousel .fancybox__slide.has-html5video .fancybox__content.has-video {
  height: 100% !important;
}
.select {
  position: relative;
  &--active {
    z-index: 10;
    .select-arrow svg {
      transform: rotate(-180deg);
    }
    .select-body {
      transform: translateY(0);
      opacity: 1;
      pointer-events: auto;
    }
  }
  &-head {
    position: relative;
    z-index: 7;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    background: #F1F7F7;
    border-radius: 8px;
    cursor: pointer;
  }
  &-current {
    line-height: 120%;
    color: #006B6B;
  }
  &-arrow {
    width: 12px;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    svg {
      width: 100%;
      height: auto;
      transition: all 0.3s;
    }
  }
  &-body {
    position: absolute;
    z-index: 6;
    top: calc(100% + 4px);
    transform: translateY(-8px);
    left: 0;
    width: 100%;
    background: #F1F7F7;
    border-radius: 8px;
    padding: 20px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.125s;
    &:after {
      content: '';
      position: absolute;
      top: -4px;
      left: 0;
      width: 100%;
      height: 4px;
      background: transparent;
    }
  }
  &-li {
    cursor: pointer;
  }
}
.pagination {
  display: flex;
  align-items: center;
  gap: 4px;
  &__items {
    display: flex;
    align-items: center;
    gap: 4px;
  }
  &__prev,
  &__next {
    width: 57px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #006B6B;
    border-radius: 10px;
    &:hover {
      background: #F1F7F7;
      svg path {
        fill: #006B6B;
      }
    }
    svg {
      max-width: 60%;
      height: auto;
      transition: all 0.3s;
    }
  }
  &__item {
    width: 57px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F1F7F7;
    border-radius: 10px;
    font-size: 16px;
    line-height: 125%;
    color: #006B6B;
    &:hover {
      background: #006B6B;
      color: #fff;
    }
  }
}

@media screen and (max-width: 1150px) {
  .h1 {
    font-size: 36px;
    letter-spacing: -0.72px;
  }
}

@media screen and (max-width: 992px) {
  .modal-price {
    .modal__dialog {
      padding: 34px;
    }
    .modal__close {
      top: 38px;
      right: 34px;
    }
    &__table {
      thead {
        tr {
          th {
            padding-right: 5px;
            &:first-child {
              width: auto;
            }
          }
        }
      }
      tbody {
        td {
          padding-right: 5px;
        }
      }
    }
  }
}

@media screen and (max-width: 798px) {
  .modal-price {
    .swiper {
      overflow: unset;
    }
    .swiper-slide {
      width: auto !important;
    }
    &__overflow {
      width: calc(100% + 34px);
      padding-right: 34px;
      overflow-x: auto;
    }
    &__table {
      width: 700px;
    }
  }
}

@media screen and (max-width: 576px) {
  .h1 {
    font-size: 30px;
    letter-spacing: -0.4px;
  }
  .h2 {
    font-size: 26px;
    letter-spacing: -0.52px;
  }
  .breadcrumbs {
    &__item {
      font-size: 14px;
    }
  }
  .form {
    &-group {
      input {
        height: 40px;
        border-radius: 6px;
      }
    }
    &-btn {
      height: 40px;
      border-radius: 6px;
      span {
        font-size: 14px;
      }
    }
  }
  .modal {
    &__icon {
      width: 140px;
      margin-bottom: 10px;
    }
    &__title {
      font-size: 20px;
    }
    &__subtitle {
      font-size: 14px;
    }
    &__btn {
      height: 34px;
      span {
        font-size: 14px;
      }
    }
  }
  .modal-price {
    .modal__dialog {
      border-radius: 15px;
      padding: 15px;
    }
    .modal__close {
      top: 17px;
      right: 15px;
    }
    .modal__title {
      font-size: 26px;
    }
    &__subtitle {
      font-size: 18px;
    }
    &__overflow {
      width: calc(100% + 15px);
      padding-right: 15px;
    }
  }
  .modal-info {
    .modal__dialog {
      border-radius: 15px;
      padding: 15px;
    }
    &__img {
      img {
        border-radius: 15px;
      }
    }
  }
  .pagination {
    &__prev,
    &__next {
      width: 40px;
    }
    &__item {
      width: 40px;
      font-size: 14px;
    }
  }
}

@media screen and (max-width: 359px) {
  .pagination {
    &__prev,
    &__next {
      width: 36px;
      height: 36px;
    }
    &__item {
      width: 36px;
      height: 36px;
    }
  }
}