.contacts-main {
  padding-bottom: 45px;
  &__wrap {
    position: relative;
    margin-top: 25px;
  }
  .map-wrap {
    position: relative;
    min-height: 450px;
    padding-bottom: 35.2%;
  }
  .map {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
      border-radius: 14px;
    }
    .ymaps-2-1-79-inner-panes {
      border-radius: 14px;
    }
  }
  &__desc {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    padding: 24px;
  }
  &__desc-inner {
    max-width: 400px;
    background: #fff;
    border-radius: 10px;
    padding: 44px 44px 34px 44px;
  }
  &__address {
    font-size: 16px;
    line-height: 150%;
  }
  &__hours {
    font-size: 16px;
    line-height: 125%;
    margin-top: 24px;
  }
  &__socials {
    margin-top: 24px;
  }
  &__social {
    &:hover {
      background: #268B8B;
      svg path {
        fill: #fff;
      }
    }
  }
  &__phones {
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
    margin-top: 6px;
  }
  &__phone {
    font-size: 18px;
    line-height: 120%;
    font-weight: 500;
  }
  &__btn {
    height: 38px;
    gap: 10px;
    border-radius: 8px;
    padding: 4px 16px;
    margin-top: 14px;
    i {
      width: 10px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 14px;
    }
  }
  &__email {
    display: inline-block;
    font-size: 16px;
    line-height: 150%;
    text-decoration: underline;
    color: #006B6B;
    margin-top: 14px;
  }
}
.route {
  padding: 45px 0;
  .tabs {
    background: #F1F7F7;
    border-radius: 20px;
    padding: 24px;
    margin-top: 20px;
  }
  .tab {
    height: 56px;
    border-radius: 8px;
    padding: 5px 15px;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
    &:hover {
      background: #fff;
      color: #191919;
    }
  }
  .tab--active {
    background: #006B6B;
    color: #fff;
    &:hover {
      background: #006B6B;
      color: #fff;
    }
  }
  .tab__content {
    display: none;
  }
  .tab__content--active {
    display: block;
  }
  .tabs-body {
    margin-top: 15px;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 25px;
    p {
      font-size: 18px;
      line-height: 155%;
    }
  }
  &__images {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 20px / 3));
    gap: 10px;
    margin-top: 25px;
  }
  &__img {
    position: relative;
    padding-bottom: 89%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 12px;
    }
  }
}
.requisites {
  padding: 45px 0;
  &__inner {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 15px / 2));
    gap: 15px;
  }
  &__left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 50px;
    background: #F1F7F7;
    border-radius: 24px;
    padding: 25px;
  }
  &__right {
    background: #006B6B;
    border-radius: 24px;
    padding: 54px;
    .requisites__title {
      color: #fff;
    }
  }
  &__title {
    font-size: 30px;
    line-height: 110%;
    font-weight: 500;
    letter-spacing: -0.6px;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 15px;
      span {
        font-size: 14px;
        line-height: 128%;
      }
      span:nth-child(1) {
        max-width: 225px;
        width: 100%;
        color: #8E8E8E;
      }
      span:nth-child(2) {
        max-width: 300px;
        width: 100%;
        font-weight: 500;
      }
    }
  }
  &__btn {
    height: 54px;
    border-radius: 10px;
    padding: 5px 15px;
    &:hover {
      background: #E6B964;
    }
    span {
      font-size: 14px;
    }
  }
  .form {
    max-width: 100%;
  }
}

@media screen and (max-width: 992px) {
  .route {
    .swiper {
      overflow: unset;
    }
    .tab {
      width: auto !important;
    }
  }
  .requisites {
    &__inner {
      grid-template-columns: 100%;
    }
    &__right {
      padding: 25px;
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts-main {
    padding-bottom: 25px;
    .map-wrap {
      min-height: 615px;
    }
    &__desc {
      top: unset;
      bottom: 0;
      width: 100%;
      padding: 14px;
    }
    &__desc-inner {
      max-width: 100%;
      padding: 14px;
    }
  }
  .route {
    padding: 25px 0;
    .tabs {
      padding: 14px;
    }
  }
  .requisites {
    padding: 25px 0;
    &__left,
    &__right {
      padding: 15px;
    }
    &__title {
      font-size: 26px;
      letter-spacing: -0.52px;
    }
  }
}

@media screen and (max-width: 359px) {
  .route {
    &__images {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
    }
  }
}