.basket {
  padding-bottom: 60px;
  &__wrap {
    display: flex;
    align-items: start;
    gap: 45px;
    margin-top: 25px;
  }
  &__left {
    width: calc(100% - 352px);
  }
  &__right {
    width: 307px;
  }
  &__total-wrap {
    background: #F2DAAF;
    border-radius: 16px;
    padding: 16px;
  }
  &__total {
    font-size: 20px;
    line-height: 100%;
    font-weight: 500;
  }
  &__total-price {
    font-size: 32px;
    line-height: 100%;
    font-weight: 600;
    margin-top: 8px;
  }
  &__nds {
    font-size: 12px;
    line-height: 100%;
    color: #808080;
    margin-top: 8px;
  }
  &__btns {
    display: grid;
    grid-template-columns: 100%;
    gap: 6px;
    margin-top: 14px;
  }
  &__btn {
    width: 100%;
    height: 56px;
    padding: 5px 10px;
    span {
      font-size: 16px;
    }
  }
  &__clear {
    text-align: center;
    font-size: 14px;
    line-height: 100%;
    text-decoration: underline;
    color: #869F9F;
    margin-top: 12px;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      text-decoration: unset;
    }
  }
  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 28px;
    &-left,
    &-right {
      display: flex;
      align-items: center;
      gap: 28px;
    }
  }
  &__subtitle {
    font-size: 14px;
    line-height: 130%;
    color: #9C9C9C;
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    margin-top: 10px;
  }
  .form {
    margin-top: 60px;
    &-items {
      display: grid;
      grid-template-columns: 100%;
      gap: 30px;
    }
    &-item {
      &__title {
        font-size: 24px;
        line-height: 120%;
        font-weight: 500;
        margin-bottom: 15px;
      }
      &__subitem {
        display: grid;
        grid-template-columns: 100%;
        gap: 15px;
      }
    }
    &-group {
      display: flex;
      align-items: center;
      gap: 15px;
      label {
        width: 190px;
        flex-shrink: 0;
        font-size: 18px;
        line-height: 120%;
      }
      input {
        max-width: 700px;
        border: 1px solid #ccdfdf;
      }
      textarea {
        max-width: 700px;
        height: 150px;
        border: 1px solid #ccdfdf;
      }
    }
    &-checkboxes {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
    }
    &-check {
      input:checked ~ label {
        &::after {
          background-image: url(../img/check-icon2.svg);
        }
      }
      label {
        padding-left: 30px;
        font-size: 18px;
        color: #191919;
        &::after {
          top: -2px;
          width: 20px;
          height: 20px;
          background-size: contain;
          border-color: #191919;
        }
      }
    }
  }
}
.basket-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 24px;
  border-bottom: 1px solid #CCDFDF;
  padding: 14px 0;
  &:last-child {
    border-bottom: unset;
    padding-bottom: 0;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 24px;
  }
  &__img {
    width: 112px;
    flex-shrink: 0;
    img {
      width: 100%;
      border-radius: 18px;
    }
  }
  &__name {
    font-size: 18px;
    line-height: 155%;
    font-weight: 500;
  }
  &__article {
    font-size: 12px;
    line-height: 200%;
    color: #9C9C9C;
    margin-top: 4px;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 24px;
  }
  &__color {
    width: 16px;
    height: 16px;
    border-radius: 50%;
  }
  &__price {
    font-size: 16px;
    line-height: 138%;
    font-weight: 500;
  }
  .cartcalc {
    width: 66px;
    height: 35px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    background: #F1F7F7;
    border-radius: 12px;
    padding: 5px;
    input {
      width: 28px;
      height: 100%;
      text-align: center;
      background: transparent;
      border: none;
      font-size: 20px;
      line-height: 120%;
      font-weight: 500;
    }
  }
  .ccalc-plus,
  .ccalc-minus {
    width: 10px;
    height: 10px;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    line-height: 100%;
    font-weight: 500;
    cursor: pointer;
  }
  &__subgroup {
    display: none;
    align-items: center;
    gap: 14px;
    margin-top: 8px;
  }
}

@media screen and (max-width: 992px) {
  .basket {
    &__wrap {
      flex-direction: column;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
    }
    &__head {
      gap: 18px;
      &-left,
      &-right {
        gap: 18px;
      }
    }
    &__total-wrap {
      text-align: center;
    }
    &__btns {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 6px / 2));
    }
  }
  .basket-card {
    gap: 18px;
    &__left {
      gap: 18px;
    }
    &__right {
      gap: 18px;
    }
  }
}

@media screen and (max-width: 767px) {
  .basket {
    .form {
      &-items {
        gap: 20px;
      }
      &-item {
        &__title {
          margin-bottom: 20px;
        }
        &__subitem {
          gap: 20px;
        }
      }
      &-group {
        flex-direction: column;
        align-items: start;
        gap: 5px;
        label {
          width: 100%;
        }
        input {
          max-width: 100%;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .basket {
    &__wrap {
      margin-top: 11px;
    }
    &__head {
      display: none;
    }
    &__cards {
      margin-top: 0;
    }
    &__btns {
      grid-template-columns: 100%;
    }
    .form {
      &-group {
        input {
          height: 58px;
        }
      }
    }
  }
  .basket-card {
    flex-direction: column;
    align-items: start;
    gap: 10px;
    &__subgroup {
      display: flex;
    }
    &__right {
      width: 100%;
      justify-content: space-between;
      gap: 10px;
      .basket-card__price,
      .basket-card__color {
        display: none;
      }
    }
    .cartcalc {
      width: 182px;
      input {
        width: 100%;
        flex-grow: 1;
      }
    }
    &__total-price {
      display: block !important;
      font-size: 20px;
    }
  }
}