.products-main {
  padding-bottom: 60px;
  &3 {
    padding: 45px 0;
  }
  &__cards {
   display: grid;
   grid-template-columns: repeat(13,calc(100% / 13 - 180px / 13));
    gap: 15px;
    margin-top: 25px;
  }
  &__btn {
    height: 52px;
    border-radius: 14px;
    padding: 5px 20px;
    margin-top: 15px;
    span {
      font-size: 14px;
    }
  }
}
.products-main2 {
  .products-main__card {
    &:after {
      display: none;
    }
    &:hover {
      .products-main__card-img {
        img {
          transform: scale(1.125);
        }
      }
    }
    .products-main__card-img {
      overflow: hidden;
      img {
        transition: all 0.5s;
      }
    }
  }
}
.products-main__card {
  position: relative;
  min-height: 255px;
  display: flex;
  align-items: flex-end;
  padding: 24px;
  @media screen and (min-width: 993px) {
    &:nth-child(1) {
      grid-column: 1/5;
      grid-row: 1/3;
  }
    &:nth-child(2) {
      grid-column: 5/11;
    }
    &:nth-child(3) {
      grid-column: 11/14;
    }
    &:nth-child(4) {
      grid-column: 5/8;
    }
    &:nth-child(5) {
      grid-column: 8/14;
    }
    &:nth-child(6) {
      grid-column: 1/4;
    }
    &:nth-child(7) {
      grid-column: 4/10;
    }
    &:nth-child(8) {
      grid-column: 10 / 14;
      grid-row: 3 / 5;
    }
    &:nth-child(9) {
      grid-column: 1/7;
    }
    &:nth-child(10) {
      grid-column: 7/10;
    }
    &:nth-child(11) {
      grid-column: 1/5;
      grid-row: 5/7;
    }
    &:nth-child(12) {
      grid-column: 5/10;
    }
    &:nth-child(13) {
      grid-column: 10/14;
    }
    &:nth-child(14) {
      grid-column: 5/8;
    }
    &:nth-child(15) {
      grid-column: 8/14;
    }
  }
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 107, 107, 0.88);
    border-radius: 24px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &:hover {
    &:after {
      opacity: 1;
    }
    .products-main__card-list {
      display: flex;
    }
  }
  &-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 24px;
    img {
      width: 100%;
      height: 100%;
      border-radius: 24px;
    }
  }
  &-desc {
    position: relative;
    z-index: 2;
  }
  &-title {
    font-size: 26px;
    line-height: 120%;
    font-weight: 500;
    letter-spacing: -0.52px;
    color: #fff;
  }
  &-list {
    display: none;
    flex-wrap: wrap;
    gap: 4px;
    margin-top: 15px;
    li {
      background: #fff;
      border-radius: 6px;
      padding: 4px 20px;
      font-size: 12px;
      line-height: 200%;
      color: #003737;
    }
  }
}

@media screen and (max-width: 1200px) {
  .products-main__card {
    @media screen and (min-width: 993px) {
      &-list {
        margin-top: 10px;
        li {
          padding: 4px 8px;
        }
      }
    }
    &-title {
      font-size: 20px;
      letter-spacing: -0.4px;
    }
  }
}

@media screen and (max-width: 992px) {
  .products-main {
   &__cards {
    grid-template-columns: repeat(2, calc(50% - 15px / 2));
   }
  }
  .products-main__card {
    min-height: 260px;
    @media screen and (min-width: 577px) {
      &:nth-child(1) {
          grid-column: 1/3;
        }
        &:nth-child(6) {
          grid-column: 1/3;
        }
        &:nth-child(11) {
          grid-column: 1/3;
        }
    }
  }
}

@media screen and (max-width: 576px) {
  .products-main {
    &3 {
      padding: 25px 0;
    }
    &__cards {
      grid-template-columns: 100%;
    }
  }
}
