.delivery {
  padding-bottom: 45px;
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 15px / 2));
    gap: 15px;
    margin-top: 25px;
  }
  &__item {
    border-radius: 24px;
    padding: 34px;
    h3 {
      font-size: 26px;
      line-height: 120%;
      font-weight: 500;
      letter-spacing: -0.52px;
      margin-bottom: 16px;
    }
    p {
      font-size: 18px;
      line-height: 155%;
    }
    p b {
      font-weight: 500;
    }
  }
}

@media screen and (max-width: 992px) {
  .delivery {
    &__items {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .delivery {
    padding-bottom: 25px;
    &__item {
      padding: 16px;
    }
  }
}