.categories-main {
  padding-bottom: 45px;
  &__wrap {
    position: relative;
  }
  &__img {
    position: relative;
    min-height: 400px;
    padding-bottom: 39.1%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 24px;
    }
  }
  &__desc {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    padding: 34px;
    color: #fff;
  }
  &__title {
    max-width: 581px;
  }
  &__text {
    max-width: 581px;
    font-size: 18px;
    line-height: 155%;
    margin-top: 15px;
  }
}
.catalog {
  padding: 45px 0;
  .tabs-head {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 24px / 4));
    gap: 8px;
    margin-top: 15px;
      .tab {
        height: 56px;
        border-radius: 8px;
        padding: 5px 10px;
        color: #006B6B;
        cursor: pointer;
    
        &:hover {
          background: #F1F7F7;
          color: #006B6B;
        }
    
        span {
          font-size: 18px;
          font-weight: 500;
        }
      }
      .tab--active {
        background: #006B6B;
        color: #fff;
    
        &:hover {
          background: #006B6B;
          color: #fff;
        }
      }
  }
  .tabs-head--select {
    display: none;
    margin-top: 15px;
    .select-head {
      height: 56px;
      padding: 5px 20px;
    }
    .select-current {
      font-size: 16px;
      font-weight: 500;
    }
    .select-body {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
    }
    .select-li {
      font-size: 16px;
      line-height: 120%;
      color: #006B6B;
    }
    .select-li--active {
      font-weight: 600;
    }
  }
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px 32px;
    margin-top: 90px;
  }
  &__subgroup {
    flex-shrink: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 24px 32px;
  }
  &__select-wrap {
    display: flex;
    align-items: center;
    gap: 14px;
  }
  &__select-text {
    font-size: 12px;
    line-height: 100%;
  }
  .select-head {
    height: 32px;
    padding: 4px 14px;
  }
  .select-current {
    font-size: 12px;
    line-height: 120%;
    font-weight: 500;
  }
  .select-body {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
  }
  .select-li {
    font-size: 12px;
    line-height: 120%;
    color: #006B6B;
  }
  .select-li--active {
    font-weight: 600;
  }
  .filter-btn {
    display: none;
    align-items: center;
    gap: 14px;
    span {
      font-size: 12px;
      line-height: 100%;
    }
    i {
      width: 20px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 25px;
    margin-top: 25px;
  }
  &__left {
    width: 300px;
  }
  &__right {
    width: calc(100% - 325px);
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 20px / 3));
    gap: 10px;
  }
  .pagination {
    justify-content: flex-end;
    margin-top: 25px;
  }
  &__links {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    gap: 8px;
    margin-top: 56px;
  }
  &__link {
    border-radius: 8px;
    padding: 4px 14px;
    font-size: 12px;
    line-height: 200%;
    color: #006B6B;
  }
  .filter-title {
    display: none;
    justify-content: center;
    align-items: center;
    gap: 16px;
    font-size: 30px;
    line-height: 110%;
    letter-spacing: -0.6px;
    margin-bottom: 10px;
  }
  .filter-close {
    width: 22px;
    display: flex;
    align-items: center;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  .tab__content {
    display: none;
  }
  .tab__content--active {
    display: block;
  }
}
.catalog-sidebar {
  &__item {
    border-bottom: 1px solid #F1F1F1;
    padding: 24px 0;
    &:first-child {
      padding-top: 0;
    }
    &:last-child {
      border-bottom: none;
      padding-bottom: 0;
    }
  }
  &__subtitle {
    font-size: 14px;
    line-height: 155%;
    font-weight: 600;
  }
  .colors-item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 14px;
  }
  &__colors {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 11px;
  }
  &__color {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      border-color: #1F8C8C;
    }
    label {
      width: 22px;
      height: 22px;
      display: block;
      border: 5px solid transparent;
      border-radius: 50%;
    }
  }
  .material-item {
    display: flex;
    flex-direction: column;
    gap: 14px;
  }
  &__materials {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 8px / 2));
    gap: 8px;
  }
  &__material {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      background: #006B6B;
      color: #fff;
    }
    label {
      height: 38px;
      border-radius: 10px;
      font-size: 14px;
      transition: all 0.3s;
      &:hover {
        background: #F1F7F7;
      }
    }
  }
  &__btn {
    width: 100%;
    height: 56px;
    border-radius: 10px;
    padding: 5px 10px;
    color: #191919;
    span {
      font-size: 18px;
    }
  }
  &__btn2 {
    width: 100%;
    height: 56px;
    border-radius: 10px;
    padding: 5px 10px;
    margin-top: 8px;
    span {
      font-size: 18px;
    }
  }
  .slider-inputs {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 8px / 2));
    gap: 8px;
    margin-top: 14px;
  }
  .slider-input {
    position: relative;
    &__text {
      position: absolute;
      top: 50%;
      left: 20px;
      transform: translate(0, -47%);
      display: flex;
      align-items: center;
      font-size: 14px;
      line-height: 120%;
      color: #191919;
    }
    input {
      width: 100%;
      height: 38px;
      background: transparent;
      border: 1px solid #A8C3C3;
      border-radius: 10px;
      padding: 0 20px 0 40px;
      font-size: 14px;
      line-height: 120%;
      color: #191919;
    }
  }
  .slider {
    margin-top: 25px;
  }
  .noUi-target {
    height: 3px;
    background: #CCDFDF;
    border-radius: 0px;
    border: none;
    box-shadow: unset;
  }
  .noUi-connect {
    background: #006B6B;
  }
  .noUi-handle {
    width: 16px;
    height: 16px;
    background: #E6B964;
    box-shadow: unset;
    border: none;
    border-radius: 50%;
    &:before,
    &:after {
      display: none;
    }
  }
  .noUi-handle-lower {
    right: -16px;
  }
  .noUi-handle-upper {
    right: 0;
  }
}

@media screen and (max-width: 1200px) {
  .catalog {
    &__left {
      width: 260px;
    }
    &__right {
      width: calc(100% - 285px);
    }
  }
}

@media screen and (max-width: 992px) {
  .categories-main {
    &__img {
      img {
        border-radius: 20px;
      }
    }
  }
  .catalog {
    .tabs-head {
      display: flex;
      flex-wrap: wrap;
        .tab {
          height: 48px;
          padding: 5px 20px;
        }
    }
    &__group {
      flex-direction: column;
      align-items: start;
      margin-top: 50px;
    }
    &__subgroup {
      width: 100%;
    }
    .filter-btn {
      display: flex;
    }
    &__wrap {
      flex-direction: column;
    }
    &__left {
      position: fixed;
      z-index: 10;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #fff;
      padding: 25px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
    &__right {
      width: 100%;
    }
    .pagination {
      justify-content: center;
    }
    &__links {
      margin-top: 25px;
    }
    .filter-title {
      display: flex;
    }
  }
}

@media screen and (max-width: 768px) {
  .catalog {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
    }
  }
}

@media screen and (max-width: 576px) {
  .categories-main {
    padding-bottom: 25px;
    &__img {
      min-height: 260px;
      img {
        border-radius: 14px;
      }
    }
    &__desc {
      padding: 24px;
    }
    &__text {
      font-size: 14px;
    }
  }
  .catalog {
    padding: 25px 0;
    .tabs-head {
      display: none;
    }
    .tabs-head--select {
      display: block;
    }
    &__subgroup {
      gap: 24px 4px;
    }
  }
}

@media screen and (max-width: 359px) {
  .catalog {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}