.production {
  padding-bottom: 45px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    gap: 40px;
    margin-top: 25px;
  }
  &__slider {
    width: calc(100% - 480px);
  }
  &__desc {
    width: 440px;
    p {
      font-size: 18px;
      line-height: 155%;
    }
    b {
      display: block;
      font-size: 14px;
      line-height: 125%;
    }
    h3 {
      font-size: 26px;
      line-height: 120%;
      font-weight: 500;
      letter-spacing: -0.52px;
      margin-bottom: 16px;
    }
    hr {
      height: 48px;
      padding: 0;
      margin: 0;
      opacity: 0;
      pointer-events: none;
    }
    ul {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 36px / 2));
      gap: 36px;
      li {
        display: flex;
        flex-direction: column;
        align-items: start;
      }
      li i {
        width: 60px;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #006B6B;
        border-radius: 16px;
        img {
          max-width: 30px;
        }
      }
      li p {
        font-size: 14px;
        line-height: 130%;
        margin-top: 16px;
      }
    }
  }
  @media screen and (min-width: 993px) {
    .swiper-wrapper {
      transform: unset !important;
      flex-direction: column;
      gap: 24px;
    }
  }
  .swiper-slide {
    img {
      width: 100%;
      border-radius: 24px;
    }
  }
}

@media screen and (max-width: 992px) {
  .production {
    &__wrap {
      flex-direction: column;
    }
    &__slider {
      width: 100%;
    }
    &__desc {
      width: 100%;
    }
    .swiper {
      overflow: unset;
    }
  }
}

@media screen and (max-width: 576px) {
  .production {
    padding-bottom: 25px;
  }
}