.product-main {
  padding-bottom: 45px;
  &__wrap {
    display: flex;
    align-items: start;
    gap: 44px;
    margin-top: 25px;
  }
  .gallery {
    position: relative;
    width: 520px;
    padding-left: 110px;
  }
  .gallery__small {
    position: absolute;
    top: 0;
    left: 0;
    width: 104px;
    height: 100%;
    .swiper-slide img {
      width: 100%;
      height: 100%;
      border-radius: 10px;
    }
    .swiper-slide-thumb-active {
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);
        border-radius: 10px;
        pointer-events: none;
      }
    }
    .play {
      height: 26px;
    }
  }
  .gallery__big {
    .swiper-slide {
      padding-bottom: 104%;
      .play {
        width: 60px;
        height: 60px;
      }
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 24px;
      }
    }
  }
  .swiper-slide {
    position: relative;
    .play {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 26px;
      img {
        width: 100%;
        height: auto;
      }
    }
  }
  &__desc {
    width: calc(100% - 564px);
    display: flex;
    align-items: start;
    gap: 44px;
  }
  &__desc-left {
    width: calc(100% - 344px);
  }
  &__desc-right {
    width: 300px;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 14px;
    li {
      display: flex;
      justify-content: space-between;
      align-items: start;
      gap: 14px;
      span:nth-child(1) {
        max-width: 85px;
        width: 100%;
        flex-shrink: 0;
        font-size: 14px;
        line-height: 128%;
        color: #919191;
      }
      span:nth-child(2) {
        max-width: 238px;
        width: 100%;
        font-size: 14px;
        line-height: 128%;
        font-weight: 500;
      }
    }
    .catalog-sidebar__colors {
      gap: 4px 8px;
    }
    .catalog-sidebar__color {
      width: auto !important;
      label {
        width: 16px;
        height: 16px;
        border-width: 3px;
      }
    }
  }
  &__btns {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 8px / 2));
    gap: 8px;
  }
  &__btn {
    height: 40px;
    gap: 12px;
    background: transparent;
    border-radius: 8px;
    border: 1px solid #D6ECEC;
    padding: 4px 8px;
    &:hover {
      background: #F1F7F7;
    }
    i {
      max-width: 14px;
      flex-shrink: 0;
      svg {
        max-width: 100%;
      }
    }
    span {
      font-size: 14px;
      line-height: 100%;
    }
  }
  &__group {
    background: #F2DAAF;
    border-radius: 16px;
    padding: 16px;
    margin-top: 8px;
  }
  &__price {
    font-size: 32px;
    line-height: 125%;
    font-weight: 600;
    letter-spacing: -0.64px;
  }
  &__btns2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 6px;
    margin-top: 14px;
  }
  &__btn2 {
    height: 60px;
    gap: 8px;
    i {
      max-width: 25px;
      img {
        max-width: 100%;
      }
    }
    span {
      font-size: 16px;
    }
  }
  &__list2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 4px;
    background: #F1F7F7;
    border-radius: 16px;
    padding: 8px;
    margin-top: 8px;
    li {
      background: #fff;
      border-radius: 6px;
      padding: 8px 16px;
      font-size: 14px;
      line-height: 125%;
      strong {
        font-weight: 500;
      }
    }
  }
  .tabs {
    margin-top: 90px;
  }
  .tabs-body {
    margin-top: 24px;
  }
  .tab {
    height: 56px;
    border-radius: 8px;
    padding: 5px 10px;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
    &:hover {
      background: #F1F7F7;
      color: #191919;
    }
  }
  .tab--active {
    background: #006B6B;
    color: #fff;
    &:hover {
      background: #006B6B;
      color: #fff;
    }
  }
  .tab__content {
    display: none;
    .product-main__list {
      max-width: 375px;
    }
  }
  .tab__content--active {
    display: block;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    p {
      font-size: 18px;
      line-height: 155%;
    }
  }
}
.products--2 {
  padding-bottom: 60px;
}

@media screen and (max-width: 1400px) {
  .product-main {
    &__wrap {
      gap: 30px;
    }
    .gallery {
      width: 450px;
    }
    &__desc {
      width: calc(100% - 480px);
      gap: 30px;
    }
    &__desc-left {
      width: calc(100% - 330px);
    }
    &__desc-right {
      width: 300px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .product-main {
    &__desc-left {
      width: calc(100% - 290px);
    }
    &__desc-right {
      width: 260px;
    }
    @media screen and (min-width: 993px) {
      &__btns {
        grid-template-columns: 100%;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .product-main {
    .gallery {
      width: 400px;
    }
    &__desc {
      width: calc(100% - 430px);
    }
    @media screen and (min-width: 993px) {
      &__list {
        gap: 10px;
        li {
          gap: 10px;
          span {
            font-size: 12px !important;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .product-main {
    &__desc-left {
      width: calc(100% - 270px);
    }
    &__desc-right {
      width: 240px;
    }
  }
}

@media screen and (max-width: 992px) {
  .product-main {
    &__wrap {
      flex-direction: column;
      gap: 44px;
    }
    .gallery {
      width: 100%;
      padding: 0;
    }
    .gallery__small {
      position: static;
      width: 100%;
      margin-top: 6px;
      .swiper-slide {
        padding-bottom: 18%;
        img {
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }
    .gallery__big {
      .swiper-slide {
        padding-bottom: 56%;
      }
    }
    &__desc {
      width: 100%;
    }
    &__desc-left {
      width: calc(100% - 330px);
    }
    &__desc-right {
      width: 300px;
    }
    &__btn2 {
      height: 48px;
      span {
        font-size: 16px;
      }
    }
    .tab__content {
      .product-main__list {
        max-width: 100%;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .product-main {
    padding-bottom: 25px;
    .gallery__big {
      .swiper-slide {
        min-height: 210px;
      }
    }
    &__desc {
      flex-direction: column;
    }
    &__desc-left {
      width: 100%;
    }
    &__desc-right {
      width: 100%;
    }
    &__list {
      gap: 10px;
    }
    .tabs {
      margin-top: 50px;
    }
    .tabs-head {
      .swiper {
        overflow: unset;
      }
      .swiper-slide {
        width: auto !important;
      }
    }
    .tab {
      height: 36px;
      padding: 4px 14px;
      font-size: 14px;
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
  }
}