.services-main {
  padding-bottom: 45px;
  &__cards {
    display: grid;
    grid-template-columns: 1.4fr 0.6fr;
    grid-template-rows: 1fr 1fr;
    gap: 15px;
    margin-top: 15px;
  }
  &__btns {
    display: grid;
    grid-template-columns: 1.4fr 0.6fr;
    gap: 15px;
    margin-top: 15px;
  }
  &__price {
    height: 76px;
    padding: 5px 15px;
    span {
      font-size: 36px;
    }
  }
  &__btn {
    height: 76px;
    padding: 5px 10px;
    span {
      font-size: 18px;
    }
  }
}
.services-main__card {
  &--big {
    grid-row: 1/3;
    display: flex;
    flex-direction: column;
    .services-main__card-img {
      flex-grow: 1;
      padding-bottom: 60%;
    }
  }
  &-text {
    background: #F1F7F7;
    border-radius: 24px;
    padding: 24px;
    margin-top: 15px;
    p {
      font-size: 16px;
      line-height: 150%;
    }
  }
  &-img {
    position: relative;
    height: 100%;
    padding-bottom: 86%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 24px;
    }
  }
}
.services {
  padding: 45px 0;
  &__inner {
    width: calc(100% + 16px);
    padding: 0 8px;
    margin-left: -8px;
    overflow: hidden;
  }
  &__btns {
    margin-top: 25px;
    .swiper-wrapper {
      width: 100%;
      flex-wrap: nowrap !important;
      flex-direction: unset !important;
    }
  }
  &__btn {
    height: 52px;
    border-radius: 8px;
    padding: 5px 20px;
    span {
      font-size: 18px;
    }
  }
  .tab {
    &:hover {
      background: #F1F7F7;
      color: #191919;
    }
  }
  .tab--active {
    background: #006B6B;
    color: #fff;
    &:hover {
      background: #006B6B;
      color: #fff;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 15px / 2));
    gap: 25px 15px;
    margin-top: 25px;
  }
  &__btn2 {
    width: 100%;
    height: 64px;
    border-radius: 14px;
    padding: 5px 20px;
    margin-top: 25px;
    span {
      font-size: 18px;
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 25px;
    background: #F1F7F7;
    border-radius: 15px;
    padding: 25px;
    margin-top: 25px;
    p {
      font-size: 16px;
      line-height: 150%;
    }
  }
  .tab__content {
    display: none;
  }
  .tab__content--active {
    display: block;
  }
  &__slider,
  &__slider2 {
    margin-top: 25px;
    .swiper {
      overflow: unset;
    }
    .swiper-wrapper {
      flex-direction: row;
    }
    @media screen and (min-width: 769px) {
      .swiper-slide {
        margin: 0 25px 25px 0 !important;
      }
    }
  }
  &__slider-btns {
    display: flex;
    justify-content: flex-end;
    gap: 5px;
    margin-top: 25px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
    width: 57px;
    height: 57px;
    margin-top: 0;
  }
}
.services-card {
  background: #fff;
  border-radius: 24px;
  transition: all 0.3s;
  &:hover {
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
  }
  &2 {
    background: #F2DAAF;
    border-radius: 15px;
    padding: 24px;
    .services-card__img {
      img {
        border-radius: 10px;
      }
    }
  }
  &__img {
    position: relative;
    border-radius: 24px 24px 0 0;
    padding-bottom: 63.2%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 24px;
    }
  }
  &__desc {
    border-radius: 0 0 24px 24px;
    padding: 15px;
  }
  &__price {
    font-size: 18px;
    line-height: 155%;
    font-weight: 500;
    color: #006B6B;
    margin-top: 10px;
  }
  &__text {
    font-size: 16px;
    line-height: 150%;
    margin-top: 10px;
  }
  &__price2 {
    color: #006B6B;
    margin-top: 10px;
  }
}

@media screen and (max-width: 992px) {
  .services-main {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
      grid-template-rows: unset;
    }
    &__btns {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
    }
    &__price {
      height: 70px;
      span {
        font-size: 30px;
      }
    }
    &__btn {
      height: 70px;
    }
  }
  .services-main__card {
    &--big {
      grid-column: 1/3;
      grid-row: unset;
    }
  }
}

@media screen and (max-width: 576px) {
  .services-main {
    &__cards {
      grid-template-columns: 100%;
    }
    &__btns {
      grid-template-columns: 100%;
    }
  }
  .services-main__card {
    &--big {
      grid-column: unset;
    }
    &-text {
      border-radius: 14px;
      padding: 14px;
    }
    &-img {
      padding-bottom: 69% !important;
      img {
        border-radius: 14px;
      }
    }
  }
  .services {
    padding: 25px 0;
    &__btns {
      .swiper {
        overflow: unset;
      }
      .swiper-slide {
        width: auto !important;
      }
    }
    &__text {
      gap: 15px;
      padding: 15px;
    }
  }
  .services-card {
    border-radius: 14px;
    &2 {
      .services-card__img {
        padding-bottom: 76%;
      }
    }
    &__img {
      border-radius: 14px 14px 0 0;
      padding-bottom: 122%;
      img {
        border-radius: 14px;
      }
    }
    &__desc {
      border-radius: 0 0 14px 14px;
    }
  }
}

@media screen and (max-width: 359px) {
  .services {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}